.menuBox {
  display: flex;
  align-items: center;
  gap: 10px;
  flex-wrap: wrap;
}

.menuBox p {
  padding: 0;
}

.logout {
  background-color: black;
  color: white;
  border: 0;
  border-radius: 4px;
  padding: 5px;
  opacity: 0.5;
  cursor: pointer;
  transition: 0.5s;
}

.logout:hover {
  opacity: 1;
}
